            </el-form-item>
            <el-form-item label=":">
              <span>{{ props.row.agentInfo.sm_ai_Email}}</span>
            </el-form-item>
            <el-form-item label=":">
              <span>{{ props.row.agentInfo.sm_ai_GoodName}}</span>
            </el-form-item>
            <el-form-item label=":">
