      </el-table-column>
      <el-table-column
        label=""
        prop="agentInfo.sm_ai_ID">
      </el-table-column>
      <el-table-column
        label=""
        prop="agentInfo.sm_ai_Name">
      </el-table-column>
      <el-table-column
        label=""
        prop="agentInfo.sm_ai_Telephone">
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <!--<el-button-->
          <!--size="mini"-->
          <!--@click="updateAdminSupplier(scope.row.agentInfo.sm_ai_ID)">-->
          <!--</el-button>-->
          <el-button
            size="mini"
            type="primary"
            v-show="scope.row.agentInfo.sm_ai_IsPass!=1"
